.banner {
  background-image: url(https://images.unsplash.com/photo-1506846540181-347008948fff?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1493&q=80);
  @include bg-img-cover;
  height: 480px;
  &__box {
    padding-left: 165px;
    @include desktop {
      padding-left: 80px;
    }
    @include mobile {
      padding-left: 0px;
    }
  }
}

.nav-scroller {
  height: 2.75rem; /*  */
  overflow-y: hidden;
}
.nav-scroller .nav {
  padding-bottom: 1rem; /*  */
  overflow-x: auto;
  white-space: nowrap;
}

.menu {
  &__item {
    width: 65px;
    font-weight: normal;
  }
}

.popular {
  height: 600px;
}
